@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}
html {
  box-sizing: border-box;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #333333;
  font-style: normal;
  text-transform: uppercase;
  background-color: #ffffff;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
a {
  text-decoration: none;
  color: inherit;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 1200px) {
  .page {
    background-color: #f2f2f2;
  }
}
.page-wrapper {
  width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-wrapper {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .page-wrapper {
    width: 1200px;
    box-shadow: 5px 0 15px -7px rgba(0, 1, 1, 0.2), -5px 0 15px -7px rgba(0, 1, 1, 0.2);
  }
}
.page-header {
  width: 100%;
}
.button {
  display: block;
  box-sizing: border-box;
  padding: 16px 13px;
  border: none;
  font: inherit;
  font-size: 21px;
  font-weight: 700;
  color: #ffffff;
  line-height: 26px;
  text-align: center;
  text-transform: inherit;
  text-decoration: none;
  background-color: #81b3d2;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:focus {
  outline-color: #aee1ff;
}
.button:disabled {
  background-color: #e5e5e5;
}
.main-nav {
  position: relative;
  width: 320px;
  padding-bottom: 56px;
  background-color: #ffffff;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 12;
  display: block;
  width: 63px;
  height: 56px;
  background-color: transparent;
  border: none;
  outline-color: #aee1ff;
  cursor: pointer;
}
.main-nav--closed .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 17px;
  left: 20px;
  z-index: 20;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 20px;
  z-index: 15;
  width: 22px;
  height: 22px;
  cursor: pointer;
  background-image: url("../img/icon-cross.svg");
}
.main-nav__logo {
  position: absolute;
  z-index: 30;
  left: 50%;
  bottom: -28px;
  width: 102px;
  height: 84px;
  transform: translateX(-50%);
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--opened .main-nav__list {
    background-color: #81b3d2;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
  .main-nav--nojs .main-nav__logo {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav {
    width: auto;
    margin: 0;
    padding-bottom: 0;
  }
  .main-nav__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 768px;
    margin: 0 auto;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__logo {
    bottom: auto;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    width: 1200px;
  }
}
