.video {
  position: relative;
}

.video__image {
  display: block;
  width: 100%;
  height: auto;
}

.video__panel {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin-left: 6.25%;
  margin-right: 6.25%;
}

@media (min-width: @tablet-width) {
  .video {
    padding-left: 60px;
    padding-right: 60px;
  }

  .video__panel {
    margin-left: 60px;
    margin-right: 60px;
  }
}

@media (min-width: @desktop-width) {
  .video {
    margin: 0 auto;
    padding-left: 0;
    padding-right: 0;
    width: 800px;
  }

  .video__panel {
    margin-left: 0;
    margin-right: 0;
  }
}
